import { mapState, mapActions } from "vuex";
export default {
  name: "test",
  data() {
    return {
      itemId: null,
      choosedNum: null,
      choosedId: null
    };
  },
  computed: mapState([
    'itemNum', //第几题
    'level', //第几周
    'itemDetail', //题目详情
    'timer', //计时器
  ]),
  mounted(){
  },
  created() {
    // this.get();
    // console.log(this.$route.params.id);
  },
  methods: {
    ...mapActions([
      'addNum','initializeData'
    ]),
    nextItem(){
      if(this.choosedNum !== null){
        this.choosedNum =null;
        this.addNum(this.choosedId)
      }else{
        alert('您还没有选择答案~')
      }
    },
    answerTable(val){
      switch(val){
        case 0:
          return 'A'
        case 1:
          return 'B'
        case 2:
          return 'C'
        case 3:
          return 'D'
      }
    },
    choseAnswer(index,answerId){
      this.choosedNum=index;
      this.choosedId=answerId
    },
    submitAnswer(){
      if(this.choosedNum!==null){
        this.addNum(this.choosedId)
        clearInterval();
        this.$router.push('score')
      }else{
        alert('您还没有选择答案哦~')
      }
    }
  }
};



